.Deposit {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
	font-size: "1.25rem";
	font-weight: 600;
}

.Card {
	background-color: #2f0a5b;
	border-width: 1px;
	padding: 50px;
	// width: max-content;
	width: 560px;
	/* background:linear-gradient(85deg,#f37055 -30%,#0dacf5,#f947d2,#11d1fc,#dd6ffe,#fedc6f 95%); */
}

.InnerCard {
	background-color: #1f1665;
	border: 3px solid #34bbf1;
	border-radius: 8px;
	padding: 10px;
	width: 560px;
}

.Balance {
	font-size: 56px;
	font-weight: bold;
}

.WeeklyPrize {
	color: #9183d5;
	font-size: 19px;
}

.DrawClose {
	color: #de4fce;
	font-size: 19px;
}

.DailyPrize {
	color: #1ef7d6;
	border: 1px solid #1ef7d6;
	background-color: transparent;
	font-family: Titillium Web, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji,
		Segoe UI Symbol, Noto Color Emoji;
	border-width: 2px;
	border-radius: 0.5rem;
	padding: 0.25rem 0.5rem;
	font-size: 1rem;
	font-weight: 600;
}

.DepositUSDC {
	font-size: 1.25rem;
	padding-bottom: 2rem;
	justify-content: center;
	.dropdown {
		.dropdown-toggle {
			background-color: rgba(#6c757d, 0.3);
		}
		.dropdown-menu {
			background-color: rgba(#6c757d, 0.1);
			.dropdown-item {
				display: flex;
				align-items: center;
			}
		}
	}
}

.dropBtnContainer {
	.dropdown-toggle {
		display: flex;
		align-items: center;
		.dropBtnIcon {
			align-items: center;
			display: flex;
		}
	}
}

.notchedOutline {
	border-width: 0;
}

.Amount {
	display: flex;
	align-items: flex-start;
	opacity: 0.3;
}

.USDCInput {
	left: 25px;
	top: 15px;
}

.InputValue {
	width: 100%;
	padding: 1rem;
	background-color: #1f1665;
	border-radius: 0.25rem;
	border-style: solid;
	border-color: #553399;
	text-align: right;
	font-size: 1.25rem;
	color: white;
}

.Issue {
	display: flex;
	justify-content: space-between;
	margin-top: 0.5rem;
	width: 100%;
	background-color: #1a083a;
	border-radius: 0.5rem;
	border-width: 0;
	padding: 0.5rem 1rem;
	color: white;
}
.NetworkFee {
	display: flex;
	justify-content: space-between;
	margin-top: 1rem;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.1);
	border-radius: 0.5rem;
	border-width: 0;
	padding: 0.5rem 1rem;
	color: white;
}
.ConnectButton {
	width: 100%;
	padding: 10px;
	margin-top: 1rem;
	background-color: #1ef7d6;
	color: #1a083a;
	border-radius: 0.5rem;
	cursor: pointer;
}
.Support {
	width: 560px;
	display: flex;
	flex-direction: row;
	background-color: rgba(47, 10, 91, 0.4);
}
.BackTo {
	background-color: transparent;
	border-radius: 0.5rem;
	border-style: solid;
	color: white;
}
