.NavLinkContainer{
    background-color: #5227A7;
    border-radius: 0.8rem;
    padding: 4px;
}
.NavLink{
    padding-top: 1px;
    padding-bottom: 1px;
}
.nav-link.active{
    background-color: #341762;
    border-radius: 0.5rem;
    padding-top: 1px;
    padding-bottom: 1px;
}
.CustomBtn{
    border-color: #1ef7d6;
    border-width: 2px;
    color: #1ef7d6;
    :hover{
        background-color: #1ef7d6 !important;
    }
}
.btn-outline-info:hover{
    // margin-bottom: 4px;
    background-color: #1ef7d6;
    border-color: #1ef7d6;
}
.Setting{
    color: #1ef7d6;
    cursor: pointer;
    :hover{
        color: #fff;
    }
}
